<template>
  <div id="contact-form">
    <div class="from">
      <h2>Get in touch</h2>
      <form
        class="gform"
        id="contact"
        @submit="checkForm"
        action="https://script.google.com/macros/s/AKfycbzUH1wflCGjjdw5VeX84ontoVCQhkOcgC4znXBoQawyzkqlRKYw/exec"
        method="POST"
        novalidate="true"
      >
        <div class="upper-side">
          <p>
            <input id="name" v-model="name" type="name" name="name" placeholder="Name" required />
            <br />
            <span id="error-message" v-if="nameErr === 'NAME'">Name is required.</span>
          </p>

          <p>
            <input
              id="email"
              v-model="email"
              type="email"
              name="email"
              placeholder="Email"
              required
            />
            <br />
            <span id="error-message" v-if="mailErr === 'EMAIL'">Email is required.</span>
            <span id="error-message" v-if="mailErr === 'VEMAIL'">Please enter a valid email.</span>
          </p>
        </div>
        <p>
          <br />
          <textarea
            id="message"
            name="message"
            v-model="message"
            placeholder="Your message"
            required
          ></textarea>
          <br />
          <span id="error-message" v-if="messageErr === 'MESSAGE'">Please write something.</span>
        </p>
        <p>
          <button id="submit" type="submit">{{ sender }}</button>
        </p>
        <h3
          id="ok-message"
          v-if="noerrors === 'THANKS'"
        >Thanks for contacting me! I will get back to you as soon as possible.</h3>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      nameErr: null,
      mailErr: null,
      messageErr: null,
      noerrors: null,
      name: null,
      email: null,
      message: null,
      submitStatus: null,
      sender: "Send",
    };
  },
  methods: {
    checkForm(e) {
      if (!this.name) {
        this.nameErr = "NAME";
      }
      if (!this.email) {
        this.mailErr = "EMAIL";
      } else if (!this.validEmail(this.email)) {
        this.mailErr = "VMAIL";
      }
      if (!this.message) {
        this.messageErr = "MESSAGE";
      }

      if (!this.nameErr && !this.emailErr && !this.messageErr) {
        this.sender = "Sending...";
        setTimeout(() => {
          this.sender = "Sent!";
          this.noerrors = "THANKS";
        }, 1500);
        return true;
      }

      e.preventDefault();
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
  },
};
</script>

<style lang="scss">
@import "../assets/_variables.scss";

#contact-form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 5rem 0;
  margin-bottom: -10rem;
  z-index: 5;

  .gform {
    background-color: white;
    border-radius: 10px;
    padding: 0 1rem;
    border: 1px solid;
    border-color: $p-color;

    margin: 0 auto;
    .upper-side {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
    #name {
      width: 20rem;
      height: 50px;
      border: none;
      border-bottom: 1px solid;
      padding: 8px;
      margin: 10px;
      border-color: $p-color;
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }
    #email {
      width: 20rem;
      height: 50px;
      border: none;
      border-bottom: 1px solid;
      padding: 8px;
      margin: 10px;
      border-color: $p-color;
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }
    #message {
      width: 41.5rem;
      height: 200px;
      border-radius: 5px;
      border: 1px solid;
      padding: 8px;
      margin: 10px;
      border-color: $p-color;
      &:focus {
        &::placeholder {
          font-size: 16px;
          color: rgba(0, 0, 0, 0.445);
        }
      }
    }
    #submit {
      border: 1px solid;
      border-color: $p-color;
      color: $black-color;
      width: 140px;
      height: 50px;
      border-radius: 6px;
      background: none;
      font-family: $p-font;
      transition: all 100ms ease-in-out;
      text-align: center;
      cursor: pointer;

      &:hover {
        background-color: $p-color;
        color: white;
      }
    }
    #error-message {
      color: red;
      text-align: center;
      list-style: none;
      font-family: $s-font;
      margin: 0.5rem 0;
    }
    #ok-message {
      color: green;
      font-size: 1rem;
      list-style: none;
    }
  }
}
</style>